/* Raleway regular (normal) */
@font-face {
  font-family: 'Raleway';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url('../fonts/raleway/400.woff2') format('woff2'),
    url('../fonts/raleway/400.woff') format('woff');
}

/* Raleway medium (normal) */
@font-face {
  font-family: 'Raleway';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url('../fonts/raleway/500.woff2') format('woff2'),
    url('../fonts/raleway/500.woff') format('woff');
}